.skill-node {
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: fit-content;
  border-radius: 9999px;
  background: transparent;
  border: solid transparent 1px;
  box-shadow: none;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.skill-node:hover {
  transform: scale(1.1);
}

.react-flow__controls {
  background-color: #2e2e2e !important;
  box-shadow: none !important;
}

.react-flow__controls-button {
  background-color: #2e2e2e !important;
  fill: #ffffff !important;
  border: 1px solid rgb(80, 80, 80) !important;
}

.react-flow__controls-button:hover {
  background-color: #333333 !important;
}

#tree-size {
  width: 140px;
  height: 140px;
}

#color-size {
  width: 80px;
  height: 80px;
  transition: transform 0.2s ease;
}

#color-size:hover {
  transform: scale(0.9);
  cursor: grab;
}

.text-node {
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: fit-content;
  border-radius: 9999px;
  background: transparent;
  border: solid transparent 1px;
  box-shadow: none;
  cursor: pointer;
  transition: transform 0.2s ease;
  font-size: 80px;
  color: white;
  line-height: 1.2;
}

.caption-node {
  display: flex;
  align-items: left;
  justify-content: left;
  width: 800px;
  height: fit-content;
  border-radius: 9999px;
  background: transparent;
  border: solid transparent 1px;
  box-shadow: none;
  cursor: pointer;
  transition: transform 0.2s ease;
  font-size: 50px;
  color: white;
}

.color-node {
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: fit-content;
  border-radius: 9999px;
  border: solid transparent 1px;
  box-shadow: none;
  cursor: pointer;
  background: white;
}

.react-flow__attribution {
  font-family: Fira Sans;
  font-size: 12px;
  font-weight: 200;
  color: rgb(192, 192, 192) !important;
  background-color: transparent !important;
}